*
{
   font-family: 'Noto Sans JP', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box; 
    list-style: none; 
}

body{
    background-color: white;
    color: black;
    list-style: none;
    padding: 0;
}

.container-res{
    width: 100%;
    height: 100%;
    padding: 9%;
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    float: left;
    background-image: url('../Images/resfinalbg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.container-res :first-child{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 700px;
    height: 1200px;
    background-color: whitesmoke;
}

.container-res .res-right{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 700px;
    height: 1200px;
    background: whitesmoke;
}

.container-res .res-left .left-details{
    width: 100%;
    background: transparent;
    height: 100px;
}

.container-res .res-left .left-details .res-circle img{
    width: 350px;
    height: 350px;
    border-radius: 200px; 
    object-fit: cover;
    margin-right: auto;
    margin-left: auto;
    margin-top: 5%;
    border: 2px orangered;

}

.container-res .res-left .left-details .res-circle .contact-me{
    margin-top: 5%;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: 50px;
    background: transparent;
}

.container-res .res-left .left-details .res-circle .contact-me p{
    width: 100%;
    height: 40px;
    margin-top: -1150px;
   
}
