
body{
    background-color: white;
    color: black;
    list-style: none;
    padding: 0;
}

.home-container {
    width: 100%;
    padding: 9%;
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    float: left;
    background-image: url('../Images/bg.jpg');
    background-color: rgba(255, 255, 255, 0.2);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}


.home-container .h-left{
    padding: 1%;
    display: flex;
    float: left;
    margin-top: -50px;
    width: 100%;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 20px;
}

.home-container .h-left :first-child{
    background: transparent;
    width: 30%;
    height: 599px;
}

.home-container .h-left .left-welcome{
    width: 70%;
    background: rgba(255,69,0,0.8);
    height: 595px;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    padding: 8%;
    
}

.home-container .h-left .left-img img{
    position: relative;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100%;
    width: 100%;
    object-fit: fill;
    padding: 4.5%;
    margin-left: 15%;
    border-radius: 30px;
}

.home-container .h-left .left-welcome .welcome-div{
    width: 100%;
    padding: 5%;
    height: 60px;
    background: transparent;
    justify-content: center;
    align-items: center;
    text-align: center;
    justify-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
}

.home-container .h-left .left-welcome .welcome-div h2{
    width: 100%;
    color: white;
    text-align: center;
    font-size: 30px;
}

.home-container .h-left .left-welcome .name-div{
    width: 100%;
    padding-left: 5%;
    padding-right: 5%;
    height: 60px;
    background: transparent;
    justify-content: center;
    align-items: center;
    text-align: center;
    justify-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
}

.home-container .h-left .left-welcome .name-div h4{
    margin-top: 1%;
    width: 100%;
    color: white;
    text-align: center;
    font-size: 25px;
    font-weight: 300;
}

.home-container .h-left .left-welcome .button-div{
    display: flex;
    float: left;
    width: 100%;
    padding-left: 5%;
    padding-right: 5%;
    height: 60px;
    background: transparent;
    justify-content: center;
    align-items: center;
    text-align: center;
    justify-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
}

.home-container .h-left .left-welcome .button-div .download-cv {
    margin-top: 15px;
    width: 150px;
    height: 40px;
    background: whitesmoke;
    border-radius: 30px;
    padding: 4px;
    text-align: center;
    margin-right: 15px;
    color: black;
}

.home-container .h-left .left-welcome .button-div .contact-me {
    margin-top: 15px;
    width: 150px;
    height: 40px;
    background: black;
    border-radius: 30px;
    padding: 4px;
    text-align: center;
    color: white;
    font-weight: 500;
}

.home-container .h-left .left-welcome .button-div .contact-me:hover {
    margin-top: 15px;
    width: 120px;
    height: 40px;
    background: white;
    border-radius: 30px;
    padding: 4px;
    text-align: center;
    color: black;
    font-weight: 500;
}

.home-container .h-left .left-welcome .button-div .download-cv:hover {
    margin-top: 15px;
    width: 120px;
    height: 40px;
    background: transparent;
    border-radius: 30px;
    padding: 4px;
    text-align: center;
    margin-right: 15px;
    color: white;
}

.home-container .h-left .left-welcome .icons-div{
    display: flex;
    float: left;
    width: 100%;
    padding-left: 5%;
    padding-right: 5%;
    height: 60px;
    background: transparent;
    justify-content: center;
    align-items: center;
    text-align: center;
    justify-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
}

.home-container .h-left .left-welcome .icons-div .icons{
    margin-top: 10%;
    background: transparent;
    padding: 2%;
    width: 100%;
    height: 90px;
    color: white;
}

.home-container .h-left .left-welcome .icons-div .icons img{
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 15px;
}