body{
    background: transparent;
    list-style: none;
    padding: 0;
    justify-content: center;
    align-items: center;
}

.prof-container{
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    float: left;
    position: relative;
    width: 70%;
    background: transparent;
    color: white;
    margin-top: 5%;
    margin-bottom: 5%;
    margin-left: 10%;
    margin-right: 10%;
    padding: 10px;
}

