.prof-container .prof-right{
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;
    background: orangered;
    width: 90%;
    padding: 5%;
}

.prof-container .prof-right h5{
    font-style: normal;
    font-size: 18px;
    font-weight: 700;
    padding-left: 4%;
    padding-right: 4%;
    padding-top: 4%;
    padding-bottom: 1%;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    color: white;
}

.prof-container .prof-right .contact-details{
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 2%;
    background: rgb(249,241,240);
    border-radius: 15px;
}

.prof-container .prof-right .contact-details p{
    color: black;
    padding-top: 5%;
    margin-top: 2%;
}

.prof-container .prof-right .contact-details h3{
    padding-top: 5%;
    margin-top: 2%;
    font-size: 15px;
    text-align: center;
    color: black;
}

.prof-container .prof-right .form-wrapper{
    width: 60%;
    padding: 3%;
    box-sizing: border-box;
    margin: auto;
}

.prof-container .prof-right .form-wrapper .input-field{
    padding: 2%;
    margin: 1%;
    width: 400px;
    border-radius: 10px;
    border: none;
}

.prof-container .prof-right .submit button{
    border: none;
    width: 200px;
    margin-top: 5%;
    padding-left: 5%;
    padding-right: 5%;
    height: 40px;
    background: white;
    border-radius: 15px;
    justify-content: center;
    align-items: center;
    text-align: center;
    justify-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    color: black;
    font-size: 18px;
    font-weight: 500;
}


.prof-container .prof-right .submit button:hover{
    background: rgb(63, 63, 63);
    border-radius: 15px;
    justify-content: center;
    align-items: center;
    text-align: center;
    justify-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    color: white;
    font-size: 18px;
    font-weight: 500;
}


ul li{
    padding: 4%;
    color: black;
    list-style: none;
    list-style-type:none;
    justify-content: center;
}

.fab{
    padding: 4%;
    color: black;
    list-style: none;
    list-style-type:none;
    justify-content: center;
    align-items: center;
    justify-items: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}